import React from "react"
import Helmet from "react-helmet";
import {Card, Container} from 'react-bootstrap'


class Aftermath extends React.Component {

    render() {
        return (
            <>
                <Helmet>
                     <title>Aftermath Blog</title>
                </Helmet>

                <Container style={{display: "flex", flexDirection: "row"}}>
                    <Card style={{display: "flex", flexDirection: "column", border:"none", padding: 10}}>
                        <br/>
                        <Card.Title> Saturday. 2 PM</Card.Title>

                        <Card.Text>
                            You arrive from a trip from the south. Broken both physically and mentally you hope that arriving home might
                            give you some peace. Unfortunatly, it's worse. Instead you are questioned about your entire life.
                            Why you don't have this, why you are not the best shape, to the point you finally just let go of all of your emotions.

                            All you wanted was a hug and just someone saying, "it's gonna be ok" but you feel nothing but coldness over your
                            shoulder, hugging a blanket as tight as possible hoping for some warmth.
                        </Card.Text>
                        <br/>

                        <Card.Title>Monday. 9 PM</Card.Title>

                        <Card.Text>
                            You arrive back on campus but are extremely heart-broken. You try to do work but you have no energy. To you, campus is 
                            dead - pouring your heart and soul into several projects for what? Nothing. 

                            This feeling physically makes you sick to point where you end up at the hospital. While you wish to become better,
                            in reality, does it matter? 
                        </Card.Text>

                        <br/>

                        <Card.Title>Friday 1 PM</Card.Title>
                        <Card.Text>
                            You finally recover from your sickness but don't feel welcomed anywhere. Walking into classes feels useless, friends 
                            see you in a different light. Things that used to excite you don't. Everyday feels like a cycle. No discovery.

                            Everyday feels like a swim in depression. You stop talking to people about the things you are excited about because some people in
                            your vincity have told you just to shut up and you are just defeated.
                        </Card.Text>

                        <Card.Title>1 Week Later. Friday 1 PM</Card.Title>

                        <Card.Text>
                            All you do now is sleep, attend class, and play video games. Nothing more. Nothing less.

                            You finish your degree in less than 2 months. Looking around. Your place is a mess but you could care less.
                            Long Depression kicks in. Days pass, you barely call home and when you do, you are reminded of Saturday. 
                        </Card.Text>

                        <Card.Text>
                            "I have to do something." you say to yourself. Grudingly, you get up for the first time in a long time and make a game plan.
                            The first thing you do is think about what went wrong. Discorving what went wrong might be the hardest thing to do, but 
                            it was necessary. You eat something of substance today - for the first time in a long time. 
                        </Card.Text>

                        <Card.Title>Sunday 3 PM</Card.Title>

                        <Card.Text>
                            You identify what may have gone wrong and how you can fix the mistakes. Reluctaly, you inform your family about what you new
                            plan is. They agree under the condition that is will be <i> the last attempt</i>. 
                        </Card.Text>

                        <Card.Text>
                            You start to email professors and hope one of them sees the passion that you have.
                        </Card.Text>

                        <Card.Title>Wednesday 10 AM</Card.Title>

                        <Card.Text>
                            One of the professors replies and welcomes you to discuss an idea at their office hours. In their office hours, 
                            to impress them, you come up with an idea in less than 5 minutes and to you suprise, it worked. You are then thrusted to meet
                            with a graduate student. 
                        </Card.Text>

                        <Card.Title>Thursday 4 PM</Card.Title>

                        <Card.Text>
                            You meet with this graduate student and the rest is history. The graduate student who was stuggling to be motivated 
                            to finish their research, sees you and your passion. You want to make it. Their passion is reigited by you. 
                            This forms a special type of work relationship that you have never encountered. The professor later admits that they are
                            impressed by your work ethic and ablity to pick material quickly.
                        </Card.Text>

                        <Card.Title>January 31st of the next year</Card.Title>

                        <Card.Text>
                            You have been working with this professor for nearly 8 months. They see what you are capable of. 
                            They choose you. You finally rest after a long journey...
                        </Card.Text>


                    </Card>

                </Container>
            </>
        )
    }
}

export default Aftermath;