import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import { Helmet } from 'react-helmet';
import {Card, Container} from 'react-bootstrap'


class Admissions extends React.Component {

  
  render() {
    return (
      <>
        <Helmet>
          <title>Admissions Page</title>
        </Helmet>


        <Container style={{display: "flex", flexDirection: "row"}}>
          <Card style={{display: "flex", flexDirection: "column", border:"none", padding: 10}}>
            <br/>
              <Card.Title style={{fontSize: 30}} >CS PhD Admissions in a nutshell</Card.Title>
              <hr/>
              <p> I went through the admissions <i>twice</i> and it would be irresponsible for me not to give advice to next generation of potential researchers. 
              So here how I got into 4 top CS PhD programs.</p>
              <hr/>

              <Card.Title>Is a PhD the right path for you?</Card.Title>
              <p> Answering the question of whether a PhD is for you or not is not an easy one. But there are some signs that suggest that you should consider it</p>
              <p> <b>1. You plan to become a professor</b></p>
              <p>The only way you can do this is by obtaining a PhD</p>
              <p> <b>2. You want to build the next ChatGPT / Stable Diffusion, etc (foundation model) aka become a research scientist (RS) </b></p>
              <p>You will almost certainly need a PhD to do.</p>
             
              <br/>


              <p>By far these are the 2 biggest reasons why people pursue their PhD. Personally, for me, I wanted to work on innovative products and 
                I felt a PhD would allow me to do so. Software Engineering (SWE) does not allow to you to work on projects like this unless you are on a special team which 
                tends to require very senior level. Also, I hated SWE.</p>

               
              <p> Here are some reasons <i>not</i> to do a PhD</p>
              <p> <b>1. You want a pay increase</b></p>
              <p>It is true that RS do make a lot of money, but it is not substanial. If you really wanted money, you should be a medical doctor. You will be broke during grad school for the most part. You are better off jumping the corporate ladder as SWE</p>
              <p> <b>2. You want to work on Machine Learning, but finetuning models and apply them to a dataset / task.</b></p>
              <p>You are better off getting a masters and becoming a Machine Learning Engineer (MLE) / Research Engineer (RE). </p>
              <p> <b>3. Your parents said so</b></p>
              <p>Worst reason ever. You will be miserable</p>
              <hr/>


            <br/>

            <Card.Title>What makes a great application?</Card.Title>
            <p>Ok so, you have decided to go for it. What do you need to do? This will larely depend on several things. In terms of least to most important:</p>
              <li>GPA</li>
              <li>Statement of Purpose (SOP)</li>
              <li>School / University you attended</li>
              <li>Previous Research Experience</li>
              <li>Letters of Reccomendation (LoRs)</li>
              <li>Personal Connections</li>

              <br/>

            <p>Starting off with GPA. Your GPA needs to be high but not a 4.0. A good rule of thumb is at least a 3.5 / 4.0. However, if you plan to pursue something really hot 
              (e.g. Machine Learning, Computer Vision, NLP, and anything that has been blessed by deep learning), I reccommend a 3.7+. If you did not have a high GPA for some
              reason (e.g medical, family emergency), then make sure you write that down in your apps. Moreover, a low GPA can be offset by good research, so I would not worry too much.
              However, if you have a really low GPA, I would reccomend getting a masters and getting a high GPA there.</p>

            <br/>
            <br/>
            <br/>


            <p>The Statement of purpose can be either really important or completely useless and not read at all. There are several guides out there explaning
              how to write your SOP and I will probably write one myself tailored towards CS. But to summarize, write your research background and what you plan to
              do in grad school. I personally reccommend suggesting a project for each professor. Again, I'll probably blog about this later. </p>


            <br/>
            <br/>
            <br/>

            <p>School prestige / ranking. If you go to a top school, you will have more of a chance of getting into a top school. This is due to the brand recongition
              effect (think Gucci, everyone knows what Gucci is). Many of these top professors <i> also went</i> to these top schools so they will trust top students. Now if you don't go a T20 school, there are 2 ways to offset this. First,
              your research record is impressive. Not completely easy, but it is a path that I have seen many students take. The second is to do a summer research project or REU.
              You can also directly contact some professors, but I'm not sure if they will reply.   
            </p>

            <br/>
            <br/>
            <br/>

            <p>Ok, now on the more important stuff. Previous Research Experience. This section really has two parts to this what research you did and what you published. </p>
            <p>First what research you did. I know there are guides out there that say: "oh, just do any research, you will get in". That is a complete lie. Again for the hot fields,
              if you want to be a PhD student in that field, you need to act like one. Therefore, you need to get into a lab that does research as closely related to what you want to do. 
              I personally made this mistake the first time I applied, I did research in Robotics and AI + Education but I applied to work on Generative Computer Vision. The result?
              I got into 0 schools. 
            </p>

            <p>
              If you want to get into a top school (T20), you need at least one first author A* paper published or in submission.
              For AI / ML, that usually means a first author paper from AAAI, CVPR, ECCV, ICCV, NeurIPS, NAACL, EMNLP, ACL, ICML, etc. Unfortunantly, the bar is getting even worse.
              To be completely safe, I would do 2 A* papers. I personally had three papers publicly avaliable (1 A* published, 1 A* submitted, and 1 low-tier paper).
            </p>


            <br/>
            <br/>
            <br/>

            <p>Letters of Reccomendation. Compared to college admissions, these letters are extemely important. You could fake your entire application up to this point, but
              these letters give the admissions proof. There are several tiers of letters. In terms of worst to best letters:</p>
              <li>F tier letter: Did well in class. This shows nothing about what research you did.</li>
              <li>E tier letter: Did well in class and final project was impressive. Not really useful if this doesn't tie into your research</li>
              <li>D tier letter: Contributed to a research project but had little to no interaction with professor. Some research experience but the professor doesn't know who you are</li>
              <li>C tier letter: Contributed to a research project, interacted with professor on some sort of basis. Professor knows who you are, what research you did, but the result is not clear / meaningful</li>
              <li>B tier letter: Worked directly on a research project, interacted with professor weekly, project was published as a non-first author paper. The work you did was meaningful / clear</li>
              <li>A tier letter: <b>Led</b> a research project and worked directly on a research project. Work was published / submitted as a first author paper. Professor knows who 
              you are and can easily write a letter for you.</li>
              <li>S tier letter: <b>Led </b> a research project and worked directly on a research project. <b>Very Well Known Professor</b> in your intended subfield knows who you 
              are and can easily write a letter for you. Work was published / submitted as a first author paper.</li>

            <br/>

            <p>Ideally, you want at least 1 S tier letter. However, being reallistic try to aim for 2 A tier letters. </p>

            <br/>
            <br/>
            <br/>

            <p>Personal Connections. If you know the professor you want to work with personally, this will make it 100x easier to get in. 
              The sad truth is knowing the right people just makes it way easier to get literally everything in life.
            </p>

            <hr/>

            <Card.Title>You mentioned that you did not get in the first time. What went wrong?</Card.Title>

            <p>This question irked for several weeks. And it really came down to several factors</p>
            <li>1. My research experience did not align with what I wanted to do. This made it hard for professors in that field to judge what I had done </li>
            <li>2. No A* paper published</li>
            <li>3. My SOP was a mess</li>
            <li>4. No personal connections with CV professors</li>
            <li>5. My mental health was at an all time low, making it really hard to concentate</li>
            <li>6. Took 20 credit hours at once. (DON'T DO THIS)</li>

            <br/>

            <p>All of this was a recipe for disaster. And my application showed that. I'll write a blog soon about what I did to fix this.</p>


          </Card>
          
        </Container>

        



      </>
    )
  }


}



export default Admissions;
